body {
  background-image: url("../images/backgrounds/background-blue.png");
  font-family: "Lato";
  color: black;
  //font-family: 'Source Code Pro';
  .wrapper {
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 30px;
    height: auto;
    padding: 45px;
    background-color: rgba(255, 255, 255, 0.4);
    text-align: center;
    h3 {
      font-weight: 500;
    }
    a {
      color: black;
    }
    a:hover {
      color: $basic-color;
      font-weight: bold;
    }
    a.button {
      display:inline-block;
      padding:0.45em 1.5em;
      border:0.1em solid #000;
      margin:0 0.3em 0.3em 0;
      border-radius:0.12em;
      box-sizing: border-box;
      text-decoration:none;
      font-weight:300;
      color:#000;
      text-align:center;
      transition: all 0.2s;
    }
    a.button:hover {
      background-color: $basic-color;
      border-color: $basic-color;
      color: white;
    }
    form.login, form.register {
      label {
        text-align: left;
        display: inline-block;
        width: 100px;
        margin-bottom: 15px;
      }
      input {
        border: 1px solid black;
        background: none;
        padding: 5px;
        width: 200px;
      }
      input[type="submit"] {
        display:inline-block;
        padding:0.45em 1.5em;
        border: 1px solid #000;
        width: 212px;
        margin-left: 100px;
        border-radius:0.12em;
        box-sizing: border-box;
        text-decoration:none;
        font-weight:300;
        color:#000;
        text-align:center;
        transition: all 0.2s;
      }
      input[type="submit"]:hover {
        background-color: $basic-color;
        border-color: $basic-color;
        color: white;
      }
    }
    .error {
      color: #ff1a0e;
      font-weight: bold;
    }
    .success {
      color: #4fab44;
      font-weight: bold;
    }

  }
}